	module.exports = {"data":{"package":{"id":"bs-reform","stars":26,"name":"bs-reform","version":"3.2.1","category":"library","flags":[],"platforms":["browser"],"description":"Reasonably making forms sound good again","keywords":["react","form validation"],"license":"MIT","updated":"2018-01-20T15:28:07.884Z","type":"published","score":0.6153663709507388,"quality":0.7918116750647496,"popularity":0.07949390980375406,"maintenance":1,"readme":"<h1 id=\"reformre\"><a href=\"#reformre\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>ReForm.re</h1>\n<p><a href=\"https://greenkeeper.io/\"><img src=\"https://badges.greenkeeper.io/Astrocoders/reform.svg\" alt=\"Greenkeeper badge\"></a>\n<a href=\"https://travis-ci.org/Astrocoders/reform\"><img src=\"https://travis-ci.org/Astrocoders/reform.svg?branch=master\" alt=\"Build Status\"></a></p>\n<p>Reasonably making forms sound good again</p>\n<h2 id=\"installation\"><a href=\"#installation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Installation</h2>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-none\"><code>yarn add bs-reform</code></pre></div>\n<p>Then add it to bsconfig.json</p>\n<div class=\"redex-codeblock\"><pre class=\"hljs lang-none\"><code><span class=\"hljs-string\">\"bs-dependencies\"</span>: [\n <span class=\"hljs-string\">\"bs-reform\"</span>\n]</code></pre></div>\n<h2 id=\"usage\"><a href=\"#usage\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Usage</h2>\n<p>Checkout <code>demo/src/app.re</code> also</p>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code><span class=\"hljs-class\"><span class=\"hljs-keyword\">module</span> <span class=\"hljs-title\">SignUpFormParams</span> = </span>{\n  <span class=\"hljs-keyword\">type</span> state = {\n    password: string,\n    email: string\n  };\n  <span class=\"hljs-keyword\">type</span> fields = [ | `password | `email | `confirmPassword];\n  <span class=\"hljs-comment\">/* (fieldName, getter, setter) */</span>\n  <span class=\"hljs-keyword\">let</span> lens = [\n    (`email, (s) =&gt; s.email, (s, email) =&gt; { ...s, email }),\n    (`password, (s) =&gt; s.password, (s, password) =&gt; { ...s, password }),\n    (`confirmPassword, (s) =&gt; s.confirmPassword, (s, confirmPassword) =&gt; { ...s, confirmPassword }),\n  ];\n};\n\n<span class=\"hljs-class\"><span class=\"hljs-keyword\">module</span> <span class=\"hljs-title\">SignUpForm</span> = <span class=\"hljs-title\">ReForm</span>.<span class=\"hljs-title\">Create</span>(<span class=\"hljs-title\">SignUpFormParams</span>);\n\n<span class=\"hljs-title\">let</span> <span class=\"hljs-title\">component</span> = <span class=\"hljs-title\">ReasonReact</span>.<span class=\"hljs-title\">statelessComponent</span>(\"<span class=\"hljs-title\">SignUp</span>\");\n\n<span class=\"hljs-title\">let</span> <span class=\"hljs-title\">make</span> = (~<span class=\"hljs-title\">signInMutation</span>, <span class=\"hljs-title\">_children</span>) =&gt; </span>{\n  ...component,\n  render: (<span class=\"hljs-number\">_</span>) =&gt; {\n    &lt;<span class=\"hljs-type\">SignUpForm</span>\n      initialState={password: <span class=\"hljs-string\">\"\"</span>, email: <span class=\"hljs-string\">\"\"</span>}\n      schema=[\n        (`password, <span class=\"hljs-type\">Required</span>),\n        (`email, <span class=\"hljs-type\">Email</span>),\n      ]\n      onSubmit=((values, ~setError, ~setSubmitting) =&gt; whatever(values, ~setError, ~setSubmitting))\n    &gt;\n      ...(\n        (\n           <span class=\"hljs-comment\">/* this is { values, errors, error }\n           * the form.error value is used if you need a global error, submitting error for instance\n           */</span>\n          ~form,\n          ~handleChange,\n          ~handleSubmit,\n          <span class=\"hljs-comment\">/* This sets the value of form.error */</span>\n          ~handleValidation <span class=\"hljs-keyword\">as</span> <span class=\"hljs-number\">_</span>,\n          <span class=\"hljs-comment\">/* A helper to get any field error */</span>\n          ~getErrorForField\n        ) =&gt;\n          &lt;<span class=\"hljs-type\">FormWrapper</span>&gt;\n            &lt;<span class=\"hljs-type\">ErrorWarn</span> error=form.error/&gt;\n            &lt;<span class=\"hljs-type\">FieldsWrapper</span>&gt;\n              &lt;<span class=\"hljs-type\">FormField</span>\n                fieldType=<span class=\"hljs-type\">FormField</span>.<span class=\"hljs-type\">TextField</span>\n                value=form.values.email\n                placeholder=<span class=\"hljs-string\">\"Email\"</span>\n                style=fieldsStyle\n                placeholderTextColor=<span class=\"hljs-type\">AppTheme</span>.<span class=\"hljs-type\">Colors</span>.blackLight\n                <span class=\"hljs-comment\">/* handleChange signature is (fields, string) =&gt; unit, so you can use right away with RN and React Web.\n                ** Just make an abstraction above to not give it an event and just the value\n                */</span>\n                onChangeText=handleChange(`email)\n              /&gt;\n              <span class=\"hljs-comment\">/* getErrorForField returns a option(string) */</span>\n              &lt;<span class=\"hljs-type\">ErrorText</span> value=getErrorForField(`password)/&gt;\n              &lt;<span class=\"hljs-type\">FormField</span>\n                fieldType=<span class=\"hljs-type\">FormField</span>.<span class=\"hljs-type\">TextField</span>\n                placeholder=<span class=\"hljs-string\">\"Password\"</span>\n                onChangeText=handleChange(`password)\n                value=form.values.password\n                style=fieldsStyle\n                placeholderTextColor=<span class=\"hljs-type\">AppTheme</span>.<span class=\"hljs-type\">Colors</span>.blackLight\n              /&gt;\n              etc\n            &lt;/<span class=\"hljs-type\">FieldsWrapper</span>&gt;\n            &lt;<span class=\"hljs-type\">RaisedButton</span> text=<span class=\"hljs-string\">\"Sign in\"</span> onPress=handleSubmit/&gt;\n          &lt;/<span class=\"hljs-type\">FormWrapper</span>&gt;\n      )\n    &lt;/<span class=\"hljs-type\">SignUpForm</span>&gt;\n  }\n}</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">module</span> <span class=\"hljs-type\">SignUpFormParams</span> =\n  <span class=\"hljs-keyword\">struct</span>\n    <span class=\"hljs-keyword\">type</span> state = {\n      password: <span class=\"hljs-built_in\">string</span>;\n      email: <span class=\"hljs-built_in\">string</span>;}\n    <span class=\"hljs-keyword\">type</span> fields = [ `password  | `email  | `confirmPassword ]\n    <span class=\"hljs-keyword\">let</span> lens =\n      [(`email, ((<span class=\"hljs-keyword\">fun</span> s  -&gt; s.email)),\n         ((<span class=\"hljs-keyword\">fun</span> s  -&gt; <span class=\"hljs-keyword\">fun</span> email  -&gt; { s <span class=\"hljs-keyword\">with</span> email })));\n      (`password, ((<span class=\"hljs-keyword\">fun</span> s  -&gt; s.password)),\n        ((<span class=\"hljs-keyword\">fun</span> s  -&gt; <span class=\"hljs-keyword\">fun</span> password  -&gt; { s <span class=\"hljs-keyword\">with</span> password })));\n      (`confirmPassword, ((<span class=\"hljs-keyword\">fun</span> s  -&gt; s.confirmPassword)),\n        ((<span class=\"hljs-keyword\">fun</span> s  -&gt; <span class=\"hljs-keyword\">fun</span> confirmPassword  -&gt; { s <span class=\"hljs-keyword\">with</span> confirmPassword })))]\n  <span class=\"hljs-keyword\">end</span>\n<span class=\"hljs-keyword\">module</span> <span class=\"hljs-type\">SignUpForm</span> = <span class=\"hljs-type\">ReForm</span>.<span class=\"hljs-type\">Create</span>(<span class=\"hljs-type\">SignUpFormParams</span>)\n<span class=\"hljs-keyword\">let</span> component = <span class=\"hljs-type\">ReasonReact</span>.statelessComponent <span class=\"hljs-string\">\"SignUp\"</span>\n<span class=\"hljs-keyword\">let</span> make ~signInMutation  _children =\n  {\n    component <span class=\"hljs-keyword\">with</span>\n    render =\n      (<span class=\"hljs-keyword\">fun</span> _  -&gt;\n         ((<span class=\"hljs-type\">SignUpForm</span>.createElement\n             ~initialState:{ password = <span class=\"hljs-string\">\"\"</span>; email = <span class=\"hljs-string\">\"\"</span> }\n             ~schema:[(`password, <span class=\"hljs-type\">Required</span>); (`email, <span class=\"hljs-type\">Email</span>)]\n             ~onSubmit:(<span class=\"hljs-keyword\">fun</span> values  -&gt;\n                          <span class=\"hljs-keyword\">fun</span> ~setError  -&gt;\n                            <span class=\"hljs-keyword\">fun</span> ~setSubmitting  -&gt;\n                              whatever values ~setError ~setSubmitting)\n             ~children:(<span class=\"hljs-keyword\">fun</span> ~form  -&gt;\n                          <span class=\"hljs-keyword\">fun</span> ~handleChange  -&gt;\n                            <span class=\"hljs-keyword\">fun</span> ~handleSubmit  -&gt;\n                              <span class=\"hljs-keyword\">fun</span> ~handleValidation:_  -&gt;\n                                <span class=\"hljs-keyword\">fun</span> ~getErrorForField  -&gt;\n                                  ((<span class=\"hljs-type\">FormWrapper</span>.createElement\n                                      ~children:[((<span class=\"hljs-type\">ErrorWarn</span>.createElement\n                                                     ~error:(form.error)\n                                                     ~children:<span class=\"hljs-literal\">[]</span> <span class=\"hljs-literal\">()</span>)\n                                                [@<span class=\"hljs-type\">JSX</span> ]);\n                                                ((<span class=\"hljs-type\">FieldsWrapper</span>.createElement\n                                                    ~children:[((<span class=\"hljs-type\">FormField</span>.createElement\n                                                                   ~fieldType:<span class=\"hljs-type\">FormField</span>.<span class=\"hljs-type\">TextField</span>\n                                                                   ~<span class=\"hljs-keyword\">value</span>:(\n                                                                   (form.values).email)\n                                                                   ~placeholder:<span class=\"hljs-string\">\"Email\"</span>\n                                                                   ~style:fieldsStyle\n                                                                   ~placeholderTextColor:<span class=\"hljs-type\">AppTheme</span>.<span class=\"hljs-type\">Colors</span>.blackLight\n                                                                   ~onChangeText:(\n                                                                   handleChange\n                                                                    `email)\n                                                                   ~children:<span class=\"hljs-literal\">[]</span>\n                                                                   <span class=\"hljs-literal\">()</span>)\n                                                              [@<span class=\"hljs-type\">JSX</span> ]);\n                                                              ((<span class=\"hljs-type\">ErrorText</span>.createElement\n                                                                  ~<span class=\"hljs-keyword\">value</span>:(\n                                                                  getErrorForField\n                                                                    `password)\n                                                                  ~children:<span class=\"hljs-literal\">[]</span>\n                                                                  <span class=\"hljs-literal\">()</span>)\n                                                              [@<span class=\"hljs-type\">JSX</span> ]);\n                                                              ((<span class=\"hljs-type\">FormField</span>.createElement\n                                                                  ~fieldType:<span class=\"hljs-type\">FormField</span>.<span class=\"hljs-type\">TextField</span>\n                                                                  ~placeholder:<span class=\"hljs-string\">\"Password\"</span>\n                                                                  ~onChangeText:(\n                                                                  handleChange\n                                                                    `password)\n                                                                  ~<span class=\"hljs-keyword\">value</span>:(\n                                                                  (form.values).password)\n                                                                  ~style:fieldsStyle\n                                                                  ~placeholderTextColor:<span class=\"hljs-type\">AppTheme</span>.<span class=\"hljs-type\">Colors</span>.blackLight\n                                                                  ~children:<span class=\"hljs-literal\">[]</span>\n                                                                  <span class=\"hljs-literal\">()</span>)\n                                                              [@<span class=\"hljs-type\">JSX</span> ]);\n                                                              etc] <span class=\"hljs-literal\">()</span>)\n                                                [@<span class=\"hljs-type\">JSX</span> ]);\n                                                ((<span class=\"hljs-type\">RaisedButton</span>.createElement\n                                                    ~text:<span class=\"hljs-string\">\"Sign in\"</span>\n                                                    ~onPress:handleSubmit\n                                                    ~children:<span class=\"hljs-literal\">[]</span> <span class=\"hljs-literal\">()</span>)[@<span class=\"hljs-type\">JSX</span> ])]\n                                      <span class=\"hljs-literal\">()</span>)[@<span class=\"hljs-type\">JSX</span> ])) <span class=\"hljs-literal\">()</span>)[@<span class=\"hljs-type\">JSX</span> ]))\n  }</code></pre>\n      </div>\n    </div>\n  \n<h2 id=\"schema\"><a href=\"#schema\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Schema</h2>\n<p>ReForm accepts a validation schema that looks like </p>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code>[\n  (fieldName, getter, validator)\n]</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ = [(fieldName, getter, validator)]</code></pre>\n      </div>\n    </div>\n  \n<p>or</p>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code>[\n  (`email, s =&gt; s.email, <span class=\"hljs-type\">Email</span>)\n]</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ = [(`email, ((<span class=\"hljs-keyword\">fun</span> s  -&gt; s.email)), <span class=\"hljs-type\">Email</span>)]</code></pre>\n      </div>\n    </div>\n  \n<h3 id=\"available-validators\"><a href=\"#available-validators\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Available validators</h3>\n<ul>\n<li><em>Custom(state => option(string))</em></li>\n</ul>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code>(`password, s =&gt; s.password, <span class=\"hljs-type\">Custom</span>(values =&gt; values.password == <span class=\"hljs-string\">\"123\"</span> ? <span class=\"hljs-type\">Some</span>(<span class=\"hljs-string\">\"Really?\"</span>) : <span class=\"hljs-type\">None</span>))</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ =\n  (`password, (<span class=\"hljs-keyword\">fun</span> s  -&gt; s.password),\n    ((<span class=\"hljs-type\">Custom</span>\n        ((<span class=\"hljs-keyword\">fun</span> values  -&gt;\n            <span class=\"hljs-keyword\">match</span> values.password = <span class=\"hljs-string\">\"123\"</span> <span class=\"hljs-keyword\">with</span>\n            | <span class=\"hljs-literal\">true</span>  -&gt; ((<span class=\"hljs-type\">Some</span> (<span class=\"hljs-string\">\"Really?\"</span>))[@explicit_arity ])\n            | <span class=\"hljs-literal\">false</span>  -&gt; <span class=\"hljs-type\">None</span>)))[@explicit_arity ]))</code></pre>\n      </div>\n    </div>\n  \n<ul>\n<li><em>Required</em></li>\n</ul>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code>(`fullName, s =&gt; s.fullName, <span class=\"hljs-type\">Required</span>)</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ = (`fullName, (<span class=\"hljs-keyword\">fun</span> s  -&gt; s.fullName), <span class=\"hljs-type\">Required</span>)</code></pre>\n      </div>\n    </div>\n  \n<ul>\n<li><em>Email</em></li>\n</ul>\n\n    <div class=\"redex-codeblock m-tabbed\">\n      <ul>\n        <li class=\"reason s-selected\"> RE\n        <li class=\"ml\"> ML\n      </ul>\n      <div class=\"codeblock reason s-selected\">\n        <pre class=\"hljs lang-reason\"><code>(`email, s =&gt; s.email, <span class=\"hljs-type\">Email</span>)</code></pre>\n      </div>\n      <div class=\"codeblock ml\">\n        <pre class=\"hljs lang-ml\"><code><span class=\"hljs-keyword\">let</span> _ = (`email, (<span class=\"hljs-keyword\">fun</span> s  -&gt; s.email), <span class=\"hljs-type\">Email</span>)</code></pre>\n      </div>\n    </div>\n  \n","homepageUrl":"https://github.com/Astrocoders/reform#readme","repositoryUrl":"https://github.com/Astrocoders/reform","npmUrl":"https://www.npmjs.com/package/bs-reform","issuesUrl":"https://github.com/Astrocoders/reform/issues","slug":"/package/bs-reform"}},"pathContext":{"id":"bs-reform"}}

